<script>
export default {
  name: "SummaryReport",
  data() {
    return {
      showResponsivenessCard: false,
      showi18NCard: false,
      showAccessibilityCard: false,
      featureListData: {},
      responsivenessReportData: {},
      i18NReportData: {},
      accessibilityReportData: {},
      pagesList: ["features", "responsiveness", "i18N", "accessibility"],
    };
  },
  mounted() {
    const featuresData = window.features;
    const tasks = featuresData[0].scenarios.length;
    this.featureListData = {
      scenarios: featuresData.length,
      tasks,
      steps: this.getSumOfTasks(featuresData[0]),
      actions: this.getSumOfActions(featuresData[0]),
    };

    if (window.complexity) {
      this.showResponsivenessCard = true;
      const responsivenessData = window.complexity;
      this.responsivenessReportData = {
        tasks: responsivenessData[0].scenarios.length,
        avgUserActionTime: this.getUserActionTimeForScript(
          responsivenessData[0]
        ),
        avgSystemDelay: this.getSystemDelayForScript(responsivenessData[0]),
        avgNetworkCalls:
          this.getNetworkCallsForScript(responsivenessData[0]) /
          responsivenessData[0].scenarios.length,
      };
    }
    if (window.accessibility) {
      this.showAccessibilityCard = true;

      const accessibilityData = window.accessibility;

      const { scenarios } = accessibilityData[0];
      this.accessibilityReportData = {
        tasks: scenarios.length,
        seriousViolations: this.getAvgNoOfViolations(scenarios, "serious"),
        criticalVialations: this.getAvgNoOfViolations(scenarios, "critical"),
        moderateViolations: this.getAvgNoOfViolations(scenarios, "moderate"),
        minorViolations: this.getAvgNoOfViolations(scenarios, "minor"),
      };
    }
    if (window.i18N) {
      this.showi18NCard = true;
      const i18nData = window.i18N;

      const avgViolations =
        this.getScriptWordCount(i18nData[0]) +
        this.getAltWordCountForScript(i18nData[0]) +
        this.getAriaLabelWordCountForScript(i18nData[0]) +
        this.getTooltipWordCountForScript(i18nData[0]) +
        this.getTitleWordCountForScript(i18nData[0]);
      this.i18NReportData = {
        tasks: i18nData[0].scenarios.length,
        languagesSupported: i18nData[0].language,
        avgViolations: avgViolations / i18nData[0].scenarios.length,
      };
    }
    if (window.browserPerformance) {
    }
  },
  methods: {
    openTab(type) {
      let displayName;
      let componentName;

      if (type === "feature") {
        displayName = "Feature List";
        componentName = "FeatureList";
      }
      if (type === "complexity") {
        displayName = "Complexity";
        componentName = "Complexity";
      }
      if (type === "accessibility") {
        displayName = "WCAG";
        componentName = "Accessibility";
      }
      if (type === "i18N") {
        displayName = "Internationalization (I18N)";
        componentName = "Internationalization";
      }

      const tabObject = {
        displayName,
        componentName,
        type,
        icon: "fa-chart-simple",
        hoverText: "",
        children: [],
        disabled: true,
      };
      this.$root.$emit("openTab", tabObject);
    },

    getSumOfTasks(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + scn.activities.length;
      });
      return sum;
    },
    getSumOfActions(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.actionMessagesList.length;
        });
      });
      return sum;
    },
    getUserActionTimeForScript(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((item) => {
            sum = sum + item.userActionTime;
          });
        });
      });
      return this.millisecondsToStr(sum / script.scenarios.length);
    },
    getSystemDelayForScript(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((msg) => {
            sum = sum + msg.systemDelay;
          });
        });
      });
      return this.millisecondsToStr(sum / script.scenarios.length);
    },
    getNetworkCallsForScript(script) {
      let sum = 0;
      script.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          act.actionMessagesList.forEach((msg) => {
            if (msg.networkEntries) {
              sum = sum + msg.networkEntries.length;
            }
          });
        });
      });
      return sum;
    },
    millisecondsToStr(milliseconds) {
      var temp = milliseconds / 1000;
      return `${temp}s`;
    },

    getWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { contents } = item;
        sum = sum + contents.length;
      });

      return sum;
    },
    getAltWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { alts } = item;
        sum = sum + alts.length;
      });

      return sum;
    },
    getTitleWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { tittle } = item;
        sum = sum + tittle.length;
      });

      return sum;
    },
    getAriaLabelWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { ariaLabels } = item;
        sum = sum + ariaLabels.length;
      });

      return sum;
    },
    getToolTipWordCount(data) {
      let sum = 0;
      data.forEach((item) => {
        const { toolTips } = item;
        sum = sum + toolTips.length;
      });

      return sum;
    },
    getActivityWordCount(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getWordCount(item.data);
      });

      return sum;
    },
    getTooltipWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getToolTipWordCount(item.data);
      });

      return sum;
    },
    getAriaLabelWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getAriaLabelWordCount(item.data);
      });

      return sum;
    },
    getAltWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getAltWordCount(item.data);
      });

      return sum;
    },
    getTitleWordCountForActivity(act) {
      let sum = 0;
      act.i18NDataList.forEach((item) => {
        sum = sum + this.getTitleWordCount(item.data);
      });

      return sum;
    },
    getScenarioWordCount(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getActivityWordCount(act);
      });

      return sum;
    },
    getTooltipWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getTooltipWordCountForActivity(act);
      });

      return sum;
    },
    getAriaLabelWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getAriaLabelWordCountForActivity(act);
      });

      return sum;
    },
    getAltWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getAltWordCountForActivity(act);
      });

      return sum;
    },
    getTitleWordCountForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + this.getTitleWordCountForActivity(act);
      });

      return sum;
    },
    getScriptWordCount(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getScenarioWordCount(scn);
      });
      return sum;
    },
    getAltWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getAltWordCountForScenario(scn);
      });
      return sum;
    },
    getAriaLabelWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getAriaLabelWordCountForScenario(scn);
      });
      return sum;
    },
    getTooltipWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getTooltipWordCountForScenario(scn);
      });
      return sum;
    },
    getTitleWordCountForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + this.getTitleWordCountForScenario(scn);
      });
      return sum;
    },
    getAvgNoOfViolations(scenario, categoryOfVialoation) {
      let sumOfViolations = 0;
      scenario.forEach((activities) => {
        activities.activities.forEach((activity) => {
          activity.accessibilityData.forEach((action) => {
            sumOfViolations =
              sumOfViolations +
              action.errors.filter(
                (item) => item.impact == categoryOfVialoation
              ).length;
          });
        });
      });
      return sumOfViolations / scenario.length;
    },
  },
};
</script>

<style>
.wrapper__div {
