<template>
  <div>
    <div class="text-center my-1">
      <v-row>
        <v-col
          cols="6"
          class="d-flex justify-left align-left mt-4"
        >
          <h1 class="darkgreen--text darken-1">Accessibility Metrics</h1>
          <!-- <h2>{{$t("components.a11y.heading")}}</h2> -->
        </v-col>
       
        <v-col
          cols="6"
          class="d-flex justify-end align-end"
        >
          <v-btn @click="categoryDialog = true">Show Category Details</v-btn>
          <!-- <v-btn @click="categoryDialog = true">{{$t("components.a11y.button")}}</v-btn> -->

        </v-col>
        <v-col cols="1" class="mt-4">
          <span class="darkgreen--text darken-1" style="font-size:medium; font-weight: 600;">  Filter by:
        </span>  
        </v-col>
        <v-col cols="4" class="d-flex justify-start align-start mt-3">
          <template>
            <v-container fluid>
              <v-select
                v-model="selectedImpacts"
                :items="impact"
                label="Impact"
                multiple
                outlined
                @change="filterBySelectedImpacts"
              >
                <template v-slot:prepend-item>
                  <v-list-item
                    ripple
                    @mousedown.prevent
                    @click="toggle"
                  >
                    <v-list-item-action>
                      <v-icon :color="selectedImpacts.length > 0 ? 'indigo darken-4' : ''">
                        {{ icon }}
                      </v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title>
                        Select All
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider class="mt-2"></v-divider>
                </template>
              </v-select>
            </v-container>
          </template>
        </v-col>  
      </v-row>

    </div>
    <v-divider class="my-4"></v-divider>

    <v-data-table
      :headers="headers"
      :items="scriptItems"
      disable-pagination
      class="elevation-1 accessiblityTable"
      hide-default-footer
      disable-sort
      style="box-shadow: 0 5px 6px -3px rgba(0, 0, 0, 0.2), 0 9px 12px 1px rgba(0, 0, 0, 0.14), 0 3px 16px 2px rgba(0, 0, 0, 0.12) !important "
    >
      <template v-slot:item.scenario="{ item }">
        <span>{{item.scenario}}</span>
      </template>

      <template v-slot:item.task="{ item }">
        <span v-if="isNaN(item.task)">{{item.task}}</span>
        <span v-else>{{item.task}}</span>
      </template>

      <template v-slot:item.step="{ item }">
        <span v-if="isNaN(item.step)">{{item.step}}</span>
        <span v-else>{{item.step}}</span>
      </template>

      <template v-slot:item.action="{ item }">
        <div v-if="isNaN(item.action)">
          <v-menu
            open-on-hover
            left
            offset-x
          >
            <template v-slot:activator="{ on, attrs }">
              <div style="display: inline-flex">
                <v-btn
                  icon
                  @click="showErrors(item.image, item.errors)"
                >
                  <v-icon small>fas fa-info</v-icon>
                </v-btn>
                <div
                  class="pl-2"
                  style="border-left: 1px solid white"
                  v-bind="attrs"
                  v-on="on"
                >
                  {{item.action}}
                </div>

              </div>
            </template>

          </v-menu>
        </div>
        <div v-else>
          {{item.action}}
        </div>

      </template>
    </v-data-table>

    <v-dialog
      v-model="categoryDialog"
      content-class="no-dialog-overflow"
      max-width="1000"
      auto-grow
      :hide-overlay="hideOverlay"
    >
      <v-card>
        <v-card-text>
          <v-simple-table>
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">
                    Category
                  </th>
                  <th class="text-left">
                    Definition
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in uniqueCategories"
                  :key="item.text"
                >
                  <td>{{ item.text }}</td>
                  <td>{{ item.value }}</td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="errorDialog"
      content-class="no-dialog-overflow"
      max-width="1000"
      auto-grow
      :hide-overlay="hideOverlay"
    >
      <v-card class="elevation-2">
        <v-card-title class="text-h5">
          <v-row>
            <v-col cols="11">Errors</v-col>
            <v-col
              cols="1"
              class="text-end"
            >
              <v-btn
                @click="errorDialog = false"
                icon
              >
                <v-icon>fas fa-close</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-card-title>
        <v-card-text>
          <v-expansion-panels focusable v-if="errors.length > 0">
            <v-expansion-panel
              v-for="(error,i) in errors"
              :key="i"
            >
              <v-expansion-panel-header>
                {{error.category}}
              </v-expansion-panel-header>
              <v-expansion-panel-content>
                <p class="my-2 grey--text">{{error.description}}</p>
                <p><b>Tags :</b>  {{ displaySepratedByComma(error.tags)}} </p>
                <p><b>Impact :</b> 
                  <span class="color_red" v-if="error.impact=='critical' || error.impact=='serious'  "> <a href="#"><span class="tag tag-critical-serious tag-lg">{{error.impact  }}</span></a></span>
                  <span class="color_yellow" v-else-if="error.impact=='moderate'"> <a href="#"><span class="tag tag-moderate tag-lg">{{error.impact  }}</span></a></span>
                  <span class="color_orange" v-else="error.impact=='minor'"> <a href="#"><span class="tag tag-minor tag-lg">{{error.impact  }}</span></a></span> 
                </p>
                <div
                  v-for="(node,index) in error.errNodes"
                  :key="index"
                >

                  <v-row>
                    <v-col
                      cols="auto"
                      v-if="baseImage !== ''"
                    >

                      <div
                        class="element-screenshot"
                        @click="expandImage(node.rectangle)"
                      >
                        <v-card
                          :ripple="false"
                          class="portrait"
                          :img="baseImage"
                          height="150px"
                          width="200px"
                        ></v-card>
                      </div>
                    </v-col>
                    <v-col cols="8">
                      <div>
                        {{node.label}}
                      </div>
                      <div class="teal--text">
                        {{node.htmlSnippet}}
                      </div>

                    </v-col>
                  </v-row>

                </div>

              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
          <div v-else-if="errors.length == 0"> 
            <h2>You have selected below filters in Impact : {{ displaySepratedByComma(selectedImpacts) }} </h2>
          </div >
        </v-card-text>
      </v-card>
    </v-dialog>

    <v-dialog
      v-model="imageDialog"
      content-class="no-dialog-overflow"
      fullscreen
      :hide-overlay="hideOverlay"
    >

      <v-card class="elevation-2">

        <v-card-text>
          <div
            @click="imageDialog = false"
            class="text-center"
          >

            <div class="element-screenshot__overlay">
              <div class="element-screenshot">
                <div class="element-screenshot__content">
                  <div
                    class="element-screenshot__image"
                    :style="{'background-image': `url(${baseImage})`, position: 'relative', width: `${dimension.width}px`, height: `${dimension.height}px`}"
                  >
                    <div
                      class="element-screenshot__element-marker"
                      :style="{ position: 'absolute', outline:'4px solid red',  width: `${rectangle.width}px`, height: `${rectangle.height}px`, top: `${rectangle.top}px`, left: `${rectangle.left}px` }"
                    >
                    </div>
                  </div>
                </div>
              </div>
            </div>

          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "Accessibility",
  data() {
    return {
      scripts: [],
      errors: [],
      baseImage: "",
      dimension: {},
      categories: [],
      uniqueCategories: [],
      categoriesMap: {},
      rectangle: {},
      errorDialog: false,
      imageDialog: false,
      categoryDialog: false,
      hideOverlay: false,
      selectedElement: "",
      scriptItems: [],
      menu: false,
      closeOnClick: false,
      headers: [],
      impact : [{ text : 'Minor',value : 'minor'},{ text : 'Moderate',value : 'moderate'},{ text : 'Serious',value : 'serious'},{ text : 'Critical',value : 'critical'}],
      selectedImpacts:['minor','moderate','serious','critical'],
      copyOfScriptItems :[]
    };
  },
  watch: {
    "$i18n.locale"(val) {
      // this.headers = [
      //   {
      //     text: this.$t("components.a11y.headers.scenarios"),
      //     value: "scenarios",
      //   },
      //   { text: this.$t("components.a11y.headers.tasks"), value: "task" },
      //   { text: this.$t("components.a11y.headers.steps"), value: "step" },
      //   { text: this.$t("components.a11y.headers.actions"), value: "action" },
      // ];
    },
  },
  mounted() {
    // this.formatCategory("`[aria-*]` attributes do not match their roles");

    if (window.accessibility) {
      const scripts = window.accessibility;

      this.scripts = scripts.map((scr) => {
        return {
          id: this.$uuid.v4(),
          scriptName: scr.scriptName,
          onlyName: this.trimScriptName(scr.scriptName),
          scenarios: scr.scenarios.map((scn) => {
            return {
              id: this.$uuid.v4(),
              scenarioName: scn.scenarioName,
              activities: scn.activities.map((act) => {
                return {
                  id: this.$uuid.v4(),
                  ...act,
                  accessibilityData: act.accessibilityData.map((step) => {
                    return {
                      id: this.$uuid.v4(),
                      ...step,
                    };
                  }),
                };
              }),
            };
          }),
        };
      });
      this.headers = [
        {
          text: "Scenarios",
          value: "scenario",
        },
        { text: "Tasks", value: "task" },
        { text: "Steps", value: "step" },
        { text: "Actions", value: "action" },
      ];
      this.createCategoriesArray();
      this.createTableData();
      this.addCategoryIssues();
      this.copyOfScriptItems = JSON.parse(JSON.stringify(this.scriptItems));

    } else {
      this.$router.push({ name: "upload" });
    }
  },
  methods: {
    createTableData() {
      this.headers = [...this.headers, ...this.categories];
      this.headers.push({ text: "Total Issues", value: "totalIssues" });

      this.scripts.forEach((scr) => {
        const script = {
          id: scr.id,
          scenario: scr.onlyName,
          task: scr.scenarios.length,
          step: this.getSumOfTasks(scr),
          action: this.getSumOfActionsForScript(scr),
        };
        this.scriptItems.push(script);

        scr.scenarios.forEach((scn) => {
          const scenario = {
            id: scn.id,
            scenario: "",
            task: scn.scenarioName,
            step: scn.activities.length,
            action: this.getSumOfActionsForScenario(scn),
          };

          this.scriptItems.push(scenario);

          scn.activities.forEach((act) => {
            const activity = {
              id: act.id,
              scenario: "",
              task: "",
              step: act.activityName,
              action: act.accessibilityData.length,
            };
            this.scriptItems.push(activity);

            act.accessibilityData.forEach(async (step) => {
              const item = {
                id: step.id,
                scenario: "",
                task: "",
                step: "",
                action: step.description,
                image: step.image ? step.image : "",
                errors: step.errors,
              };
              const ctgData = this.getCategoryData(step);
              let sum = 0;
              Object.keys(ctgData).forEach(function (key) {
                item[key] = ctgData[key];
                sum = sum + ctgData[key];
              });
              item.totalIssues = sum;
              this.scriptItems.push(item);
            });
          });
        });
      });
    },

    createCategoriesArray() {
      let categoryCount = 1;
      this.scripts.forEach((scr) => {
        scr.scenarios.forEach((scn) => {
          scn.activities.forEach((act) => {
            act.accessibilityData.forEach((step) => {
              step.errors.forEach((err) => {
                const index = this.uniqueCategories.findIndex(
                  (obj) => obj.value === err.category
                );

                if (index === -1) {
                  this.uniqueCategories.push({
                    text: `c${categoryCount}`,
                    value: err.category,
                  });
                  this.categories.push({
                    text: `C${categoryCount}`,
                    value: `c${categoryCount}`,
                  });
                  categoryCount++;
                }
              });
            });
          });
        });
      });
    },
    getCategoryData(step) {
      let data = {};

      this.uniqueCategories.forEach((ctg, index) => {
        const ctgIndex = step.errors.findIndex((i) => i.category === ctg.value);

        if (ctgIndex !== -1) {
          data[[this.uniqueCategories[index].text]] =
            step.errors[ctgIndex].errNodes.length;
        } else {
          data[[this.uniqueCategories[index].text]] = 0;
        }
      });

      return data;
    },
    addCategoryIssues() {
      this.scripts.forEach((scr) => {
        const sumOfCategoriesForScript = this.getSumOfCategoriesForScript(scr);
        let scriptSum = 0;
        const scriptIndex = this.scriptItems.findIndex((i) => i.id === scr.id);
        if (scriptIndex !== -1) {
          const self = this;
          Object.keys(sumOfCategoriesForScript).forEach(function (key) {
            self.scriptItems[scriptIndex][key] = sumOfCategoriesForScript[key];
            scriptSum += sumOfCategoriesForScript[key];
          });
          this.scriptItems[scriptIndex].totalIssues = scriptSum;
        }

        scr.scenarios.forEach((scn) => {
          const sumOfCategoriesForScenario =
            this.getSumOfCategoriesForScenario(scn);
          let scenarioSum = 0;
          const scenarioIndex = this.scriptItems.findIndex(
            (i) => i.id === scn.id
          );
          if (scenarioIndex !== -1) {
            const self = this;
            Object.keys(sumOfCategoriesForScenario).forEach(function (key) {
              self.scriptItems[scenarioIndex][key] =
                sumOfCategoriesForScenario[key];
              scenarioSum += sumOfCategoriesForScenario[key];
            });
            this.scriptItems[scenarioIndex].totalIssues = scenarioSum;
          }

          scn.activities.forEach((act) => {
            const sumOfActionsForCategories =
              this.getSumOfActionsForCategories(act);

            let activitySum = 0;
            const activityIndex = this.scriptItems.findIndex(
              (i) => i.id === act.id
            );
            if (activityIndex !== -1) {
              const self = this;

              Object.keys(sumOfActionsForCategories).forEach(function (key) {
                self.scriptItems[activityIndex][key] =
                  sumOfActionsForCategories[key];
                activitySum += sumOfActionsForCategories[key];
              });
              this.scriptItems[activityIndex].totalIssues = activitySum;
            }
          });
        });
      });
    },
    getSumOfCategoriesForScript(scr) {
      let data = [];

      scr.scenarios.forEach((scn) => {
        data.push(this.getSumOfCategoriesForScenario(scn));
      });

      return this.sumObjectsByKey(...data);
    },
    getSumOfCategoriesForScenario(scn) {
      let data = [];

      scn.activities.forEach((act) => {
        data.push(this.getSumOfActionsForCategories(act));
      });

      return this.sumObjectsByKey(...data);
    },
    getSumOfActionsForCategories(act) {
      let data = {};

      const actIndex = this.scriptItems.findIndex((item) => item.id === act.id);

      this.uniqueCategories.forEach((ctg) => {
        let sum = 0;
        for (
          let index = actIndex + 1;
          index < act.accessibilityData.length + actIndex + 1;
          index++
        ) {
          const element = this.scriptItems[index];
          sum = sum + element[ctg.text];
          continue;
        }

        data[ctg.text] = sum;
      });
      return data;
    },
    sumObjectsByKey(...objs) {
      return objs.reduce((a, b) => {
        for (let k in b) {
          if (b.hasOwnProperty(k)) a[k] = (a[k] || 0) + b[k];
        }
        return a;
      }, {});
    },

    getSumOfTasks(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        sum = sum + scn.activities.length;
      });
      return sum;
    },
    getSumOfActionsForScript(scr) {
      let sum = 0;
      scr.scenarios.forEach((scn) => {
        scn.activities.forEach((act) => {
          sum = sum + act.accessibilityData.length;
        });
      });
      return sum;
    },
    getSumOfActionsForScenario(scn) {
      let sum = 0;
      scn.activities.forEach((act) => {
        sum = sum + act.accessibilityData.length;
      });
      return sum;
    },
    getScriptErrorCount(scr) {
      let errArray = [];
      scr.scenarios.forEach((scn) => {
        errArray.push(this.getScenarioErrorCount(scn));
      });

      return this.sumArrays(errArray);
    },
    getScenarioErrorCount(scn) {
      let errArray = [];
      scn.activities.forEach((act) => {
        errArray.push(this.getActivityErrorCount(act));
      });

      return this.sumArrays(errArray);
    },
    getActivityErrorCount(act) {
      let errArray = [];
      act.accessibilityData.forEach((step) => {
        errArray.push(this.getStepErrorCount(step));
      });

      return this.sumArrays(errArray);
    },
    getStepErrorCount(step) {
      return step.errors.map((err) => {
        return err.errNodes.length;
      });
    },
    sumArrays(arrays) {
      return arrays.reduce(
        (acc, array) => acc.map((sum, i) => sum + array[i]),
        new Array(arrays[0].length).fill(0)
      );
    },

    async showErrors(image, errors) {
      this.errorDialog = true;
      this.errors = errors;
      this.baseImage = image;
      this.dimension = await this.getDimensions(image);
    },
    formatCategory(category) {
      let html = "";
      html = category.split(" ").map((s) => {
        if (s.match(/`(.*?)`/)) {
          return `<span class="test">${s.replace(/[``]+/g, "")}</span>`;
        } else {
          return s;
        }
      });

      return html.join(" ");
    },
    getDimensions(image) {
      return new Promise((resolve, reject) => {
        var img = new Image();
        img.src = image;

        img.onload = () => {
          resolve({ width: img.width, height: img.height });
        };
      });
    },
    expandImage(rect) {
      this.imageDialog = true;
      this.rectangle = rect;
    },
    trimScriptName(name) {
      return name ? name.split(".")[0] : "";
    },
    displaySepratedByComma(data) {
      return data && data.length != 1 ? data.join(", ") : data;
    },
    // computeZoomFactor(e, t) {
    //   const n = { x: t.width / e.width, y: t.height / e.height },
    //     r = 0.75 * Math.min(n.x, n.y);
    //   return Math.min(1, r);
    // },
    filterBySelectedImpacts(){
      this.scriptItems = JSON.parse(JSON.stringify(this.copyOfScriptItems));
      this.scriptItems = this.scriptItems.filter((row)=>{
        let flag = false;
        if(row.errors)
        {
          row.errors = row.errors.filter((error)=>{
             return this.selectedImpacts.indexOf(error.impact) > -1 
          });

          flag = row.errors.length > 0 ? true : false
        }
        else {
          flag = true;
        }
        return flag;
      });
    },
    toggle () {
      this.$nextTick(() => {
        let impactValues = this.impact.map((item)=> item.value);
        if (this.selectedAllImpacts) {
          this.selectedImpacts = []
        } else {
          this.selectedImpacts = impactValues.slice();
        }
      })
    },
    
  },
  computed:{
    selectedAllImpacts () {
      this.filterBySelectedImpacts();
      return this.selectedImpacts.length === this.impact.length
    },
    selectedSomeImpacts () {
      this.filterBySelectedImpacts();
      return this.selectedImpacts.length > 0 && !this.selectedAllImpacts
    },
    icon () {
      if (this.selectedAllImpacts) return 'mdi-close-box'
      if (this.selectedSomeImpacts) return 'mdi-minus-box'
      return 'mdi-checkbox-blank-outline'
    },
  }
};
</script>

<style lang="scss">
#elementText {
  width: 800px;
  height: 100px;
}
.screenshot img {
  width: 200px;
}
.expanded-image {
  /* width: 900px; */
  height: 500px;
}
.expanded-image:hover {
  cursor: zoom-out;
}
.screenshot img:hover {
  cursor: zoom-in;
}
.test {
  color: red;
}
tbody {
  tr:hover {
    background-color: transparent !important;
  }
}

.element-screenshot {
  cursor: zoom-in;
}

.element-screenshot__content {
  overflow: hidden;
  min-width: 110px;
  display: flex;
  justify-content: center;
  .element-screenshot__image {
    cursor: zoom-out;
    position: relative;
    .element-screenshot__element-marker {
      position: absolute;
      outline: 2px solid yellow;
    }
  }
}
.accessiblityTable {
  background-color: #dae8fc !important;
  box-shadow: 0 5px 6px -3px rgba(0, 0, 0, 0.2),
    0 9px 12px 1px rgba(0, 0, 0, 0.14), 0 3px 16px 2px rgba(0, 0, 0, 0.12) !important;
}
.v-data-table-header > tr > th > span {
  font-family: sans-serif;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 700 !important;
  font-size: 18px;
}
.accessiblityTable tbody {
  box-shadow: 0 5px 6px -3px rgba(0, 0, 0, 0.2),
    0 9px 12px 1px rgba(0, 0, 0, 0.14), 0 3px 16px 2px rgba(0, 0, 0, 0.12) !important;
  color: rgba(0, 0, 0, 0.6) !important;
  font-weight: 700 !important;
  font-size: 16px;
}

.color_yellow {
  color: yellow;
}

.color_orange {
  color: orange;
}

.color_red {
  color: red;
}

.tag {
    display: inline-block;
    border-radius: 3px;
    padding: .2em .5em .3em;
    border-radius: 2px;
    background: var(--tag-bg);
    color: var(--text-color);
    font-weight: 600;
    margin: .25em .1em
}
.tag-lg {
    font-size: 1.2em;
    border-radius: 4px
}
.tag-critical-serious {
    background: #dc0530;
    color: #fff
}

.tag-moderate {
    background: #ffcb2b;
    color: #12181a
}
.tag-minor{
  background: #f0db4f;
  color: #000
}
</style>